// declare var areaData

import * as utils from "./utils"

export class AreaScroll {
	constructor() {
		this.el_block = null
		this.el_list = null
		this.el_items = null
		this.itemHei = null

		this.scTimer = null
		this.inSlowAni = false //缓动停下中
		this.curTop = 0
		this.maxTop = 0 //可以设置的最高 scrollTop值  设置超过该值得到结果是 scrollTop为该值
		this.slowAniTimer = null

		this.curTopId = 0 //当前第一项的id 从0开始
		// this.itemNum = 5 //视口容纳项目数

		this.onScroll = this.onScroll.bind(this)
	}

	init() {
		this.itemHei = document.querySelector('.sarea-item').clientHeight
		this.el_items = this.el_list.getElementsByClassName('sarea-item')
		this.maxTop = this.el_list.clientHeight - this.el_block.clientHeight
		console.log(this.maxTop, 'max')


		/* kone todo : 
		1、一旦设置 document.getElementById('block-pro').scrollTop = xxx , 那么onScroll就会触发

		document.getElementById('block-pro').querySelector('.sarea-list')

		*/
		this.el_block.onscroll = this.onScroll
	}

	setCurSel(curIdx) {
		this.el_block.scrollTop = (curIdx) * this.itemHei
	}

	// please override me
	onSel() {
	}

	onScroll(ev) {
		if(this.scTimer == null) {
			if(this.inSlowAni == true) {
				return 
			}

			this.curTop = ev.target.scrollTop

			console.log('strrt', ev)
			this.scTimer = setInterval(function() {
				this.onScTimer(ev.target)
			}.bind(this), 400)
		}
		else {
			this.curTop = ev.target.scrollTop
			console.log('runing', ev.target.scrollTop)
		}
	}

	onScTimer (target) {
		if(this.curTop == target.scrollTop) {
			console.log('end', this.curTop)

			this.curTopId =  Math.round(target.scrollTop / this.itemHei) //4舍5入
			this.onSel()
			let endTop = this.itemHei * this.curTopId
			if(endTop > this.maxTop) {
				endTop = this.maxTop
			}
			if(endTop != target.scrollTop) {
				this.aniSlowTop(target, endTop)
			}

			clearInterval(this.scTimer)
			this.scTimer = null
		}
	}

	// @param target : 动画dom对象
	// @param endTop : 动画结束位置
	aniSlowTop(target, endTop) {	
		this.inSlowAni = true
		utils.addClass(target, 'block-stop')

		let dist = endTop - target.scrollTop
		var step = dist / Math.abs(dist) * 2
		console.log('step1', step)

		this.slowAniTimer = setInterval(function() {
			target.scrollTop += step			
			if( (step >= 0 && target.scrollTop >= endTop) ||
				(step < 0 && target.scrollTop <= endTop)   ) {
				target.scrollTop = endTop
				
				clearInterval(this.slowAniTimer)
				this.slowAniTimer = null 

			 	setTimeout(function() {
			 		this.inSlowAni = false
		 			console.log('eeee')		
			 	}.bind(this), 100)
			 	
			 	utils.removeClass(target, 'block-stop')
			}
		}.bind(this), 20)
	}

	//
	getCurSelName() {
		return this.el_items[this.curTopId + 2].innerHTML
	}


	addList(list) {
		if(list== null || list.length == 0) {
			this.el_list.innerHTML = ''
			return
		}

		let str = ''
		str += this.createItem('')
		str += this.createItem('')
		for(let key in list)  {
			str += this.createItem(key)
		}
		str += this.createItem('')
		str += this.createItem('')
		this.el_list.innerHTML = str
	}
	createItem(val) {
		return '<li class="sarea-item sarea-item-pro">' + val + '</li>'
	}
}






