
import {AreaScroll} from "./AreaScroll"
import {CityScroll} from "./CityScroll"

export class ProScroll extends  AreaScroll{
	constructor() {
		super()
		this.citySc = null
	}
	//给定索引值 0 -- n 返回对应的省份key值，即省份名	
	getProKey(id) {
		let count = 0
		for(let key in areaData)  {
			if(count == id) {
				return key
			}
			count++
		}
		return ''
	}

	//
	init(pid, cid, did) {
		this.el_block = document.getElementById('block-pro')
		this.el_list = this.el_block.querySelector('.sarea-list')


		this.addList(areaData)
		super.init()

		this.setCurSel(pid)

		//city 
		this.citySc = new CityScroll()
		this.citySc.init(this.getProKey(pid), cid, did)
	}

	onSel() {
		console.log('on sel pro',this.curTopId, this.getCurSelName())
		this.citySc.updateList( this.getProKey(this.curTopId) )
	}

	updateList(pid, cid, did) {	
		this.setCurSel(pid)

		this.citySc && this.citySc.updateList( this.getProKey(pid) )
	}

	//
	getSel() {
		let tmp = this.citySc.getSel()
		tmp.pro = this.getCurSelName()
		return tmp
		// return {
		// 	pro : 
		// 	city : this.citySc.getCurSelName(),
		// 	dist: this.citySc.getCurSelName()
		// }
	}
}


