import * as utils from "./utils"
import {ProScroll} from "./ProScroll"


var el_cont = null;
var proSc = null;


export var init = function() {
	var str = ''
	// str += '<div id="cont-sarea" class="cont-sarea">'
		str += '<div class="sarea">'
			str += '<div class="sarea-hd">'
				str += '<span id="btn-area-cancel" class="btn">取消</span>'
				str += '<span id="btn-area-ok" class="btn">确认</span>'
			str += '</div>'
			str += '<div class="sarea-bd">'
				str += '<div id="selmask" class="selmask"></div>'

				str += '<div id="block-pro" class="block block-pro">'
					str += '<ul class="sarea-list"></ul>'
				str += '</div>'
				str += '<div id="block-city" class="block block-city">'
					str += '<ul class="sarea-list"></ul>'
				str += '</div>'
				str += '<div id="block-dist" class="block block-dist">'
					str += '<ul class="sarea-list"></ul>'
				str += '</div>'
			str += '</div>'
		str += '</div>'
	// str += '</div>'


	var node = document.createElement("div");
	node.className = "cont-sarea"
	node.id = "cont-sarea"
	node.innerHTML = str
	document.body.appendChild(node)

	el_cont = document.getElementById('cont-sarea');
}

// 
var create = function(pid, cid, did) {
	utils.addClass(el_cont, 'cont-sarea-show') 

	proSc = new ProScroll()
	proSc.init(pid, cid, did)

	document.querySelector('#btn-area-cancel').onclick = function() {
		close()
	}

	document.querySelector('#btn-area-ok').onclick = function() {
		var ev = new Event('onSel')
		ev.data = proSc.getSel()
		el_cont.dispatchEvent(ev)
		close()	
	}
}
//
export var open = function(pid, cid, did) {
	if(pid == null) {
		pid = 0
	}
	if(cid == null) {
		cid = 0
	}
	if(did == null) {
		did = 0
	}

	if(proSc == null) {
		create(pid, cid, did)
	}	
	else {
		utils.addClass(el_cont, 'cont-sarea-show') 
		proSc.updateList(pid, cid, did)
	}
}

//
export var close = function() {
	if(proSc) {
		utils.removeClass(el_cont, 'cont-sarea-show') 
	}
	else {
		console.error('sarea 还没创建呢')
	}
}

//
export var  addSelEventListener = function(cbFunc, that) {
	el_cont.addEventListener('onSel', function(ev) {
		if(that == null) {
			cbFunc(ev.data)
		}
		else {
			that.cbFunc(ev.data)
		}

	})
}



