
import {AreaScroll} from "./AreaScroll"
import {DistScroll} from "./DistScroll"

export class CityScroll extends  AreaScroll{
	constructor() {
		super()
		this.distSc = null
		this.proKey = null
	}

	//给定索引值 0 -- n 返回对应的城市key值，即城市名	
	getCityKey(id) {
		let count = 0
		for(let key in areaData[this.proKey].items)  {
			if(count == id) {
				return key
			}
			count++
		}
		return ''
	}

	init(proKey, cid, did) {
		this.el_block = document.getElementById('block-city')
		this.el_list = this.el_block.querySelector('.sarea-list')


		this.updateList(proKey)

		super.init()

		//dist
		this.distSc = new DistScroll()
		this.distSc.init(this.proKey, this.getCityKey(cid), did )
	}

	updateList(proKey) {
		this.proKey = proKey
		this.addList( areaData[proKey].items )

		this.distSc && this.distSc.updateList(this.proKey, this.getCityKey(this.curTopId) )
	}

	onSel() {
		console.log('on sel city',this.curTopId, this.getCurSelName())
		this.distSc.updateList(this.proKey, this.getCityKey(this.curTopId) )
	}

	getSel() {
		return {
			city: this.getCurSelName(),
			dist: this.distSc.getCurSelName(),
		}
	}
}


